export default store => ({
  path: 'user/preview/:pageID',
  getComponent (nextState, cb) {
    require.ensure([], (require) => {
      const UserPreview = require('../../containers/UserPreviewContainer').default
      cb(null, UserPreview)
    })
  }
})



