
function getComponent (nextState, cb) {
  require.ensure([], (require) => {
    cb(null, require('./components/HomeView').default)
  }, 'createAct')
}

export default (store) => ({
  path: 'activity',
  childRoutes: [
    {
      path : 'create',
      getComponent
    },
    {
      path : 'edit/:activityID',
      getComponent
    }
  ]
})



