import { injectReducer } from 'store/reducers'

export default (store) => ({
  path: 'h5/edit/:pageID',

  getComponent (nextState, cb) {
    require.ensure([], (require) => {
      const Editor = require('./containers/H5MobileEditorContainer').default

      const fsDetail = require('./modules/fsDetail').default
      injectReducer(store, { key: 'fsDetail', reducer: fsDetail })

      const fsProduct = require('./modules/fsProduct').default
      injectReducer(store, { key: 'fsProduct', reducer: fsProduct })

      const fsPreview = require('./modules/fsPreview').default
      injectReducer(store, { key: 'previewData', reducer: fsPreview })

      const remotes = require('./modules/remotes').default
      injectReducer(store, { key: 'remotes', reducer: remotes })

      const eleStruct = require('./modules/eleStruct').default
      injectReducer(store, { key: 'eleStructData', reducer: eleStruct })

      cb(null, Editor)
    })
  }
})



